.exp {
  background: $c-white;
  border-radius: 30px;
  &__block_img {
    width: 184px;
    height: 166px;
    border-radius: 15px;
    margin-bottom: 40px;
    object-fit: cover;
  }
  &__title {
    text-align: center;
    font-size: 52px;
    color: $c-dark;
    font-weight: 600;
    padding-top: 125px;
    padding-bottom: 62px;
  }
  &__blocks {
    display: flex;
    justify-content: space-between;
    text-align: center;
    padding-bottom: 130px;
  }
  &__block_title {
    font-size: 20px;
    font-weight: 600;
    color: $c-dark;
    margin-bottom: 16px;
  }
  &__block_descr {
    font-size: 14px;
    color: $c-light-dark;
    font-family: Open Sans, sans-serif;
  }
}
